* {
  padding: 0;
  margin: 0;
}

body {
  /*background: linear-gradient(354deg, rgba(62,7,173,1) 10%, rgba(134,10,159,10) 35%, rgba(164,0,255,1) 100%);*/
  background: linear-gradient(
    340deg,
    rgb(19, 184, 235) 20%,
    rgb(67, 238, 238) 55%,
    rgb(122, 243, 217) 100%
  );
  background-repeat: no-repeat;
  height: 200vh;
  padding: 40px;
}

.containerCard {
  max-width: 950px;
  margin: 20px auto;
}
.containerCard .accent {
  width: 50%;
  height: 100%;

  position: absolute;
  bottom: 10px;
  left: 40vw;

  z-index: -1;
}
